.elements {
    max-width: 880px;
    margin-bottom: 66px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(282px, 1fr));
    column-gap: 17px;
    row-gap: 20px;
}

@media (max-width: 620px) {
    .elements {
        max-width: 282px;
        margin: 0 auto 48px;
    }
}